

























































































































































































































import { AppService } from "@/service/app.service";
import { RequestData, UserModel } from "@/x-vue/services/interfaces";
import Vue from "vue";
import Component from "vue-class-component";
@Component({
  components: {},
})
export default class AdminUserEdit extends Vue {
  user: UserModel = {} as UserModel;
  app = AppService.instance;

  async mounted(): Promise<void> {
    console.log("AdminUserEdit::mounted(); ");

    let options: RequestData = {
      idx: this.$route.params.userIdx ?? 0,
      full: true,
    };

    try {
      console.log(options);
      this.user = await this.app.api.userGet(options);

      console.log(this.user);
    } catch (e) {
      this.app.error(e);
    }
  }

  async onSubmit(): Promise<void> {
    let options: RequestData = {
      idx: this.user.idx,
      point: this.user.point,
      email: this.user.email,
      firebaseUid: this.user.firebaseUid,
      name: this.user.name,
      nickname: this.user.nickname,
      phoneNo: this.user.phoneNo,
      gender: this.user.gender,
      birthdate: this.user.birthdate,
      countryCode: this.user.countryCode,
      province: this.user.province,
      city: this.user.city,
      address: this.user.address,
      zipcode: this.user.zipcode,
      block: this.user.block ? "Y" : "N",
      // domain: this.user.domian,
    };

    console.log("onsubmit");

    console.log(options);
    try {
      this.user = await this.app.api.userUpdate(options);
      alert("Update Success");
      console.log(this.user);
    } catch (e) {
      this.app.error(e);
    }
  }
}
